<template>
  <div class="detailsTextColumn" v-if="isColumn">
    <div class="detailsText" v-for="(texts, key) in textArr" :key="key">
      <p v-for="text in texts" :key="text">
        {{ text }}
      </p>
    </div>
  </div>
  <div class="detailsText indent" v-else>
    <p v-for="(text, key) in textArr" :key="key">
      {{ text }}
    </p>
  </div>
</template>
    

<script>
export default {
  props: {
    isColumn: Boolean,
    textArr: Array,
  },
};
</script>

<style lang="less" scoped>
.detailsText {
  padding: 20px;
  transition: all 0.5s;
}
.indent {
  text-indent: 2rem;
}
.detailsTextColumn {
  display: flex;
  width: 100%;
  justify-content: space-around;
}

@media screen and (max-width: 375px) {
  .detailsTextColumn {
    display: flex;
    width: 100%;
    flex-direction: column;
  }
  .detailsTextColumn .detailsText {
    padding: 0px 20px;
  }
}
</style>